.switch {
  width: 46px;
  height: 20px;
  padding-top: -25px;
  display: block;
  position: relative;
}

@media screen and (max-width: 767px) {
  .switch {
    display: none;
  }
}

.switch-input {
  height: 0;
  width: 0;
  opacity: 0;
  border-radius: 35px;
}

.switch-slider {
  cursor: pointer;
  width: 46px;
  height: 20px;
  background-color: #cecdcd;
  border-radius: 60px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
}

.switch-slider:before, .burger-switch-slider:before {
  content: "";
  cursor: pointer;
  height: 18.8px;
  width: 18.8px;
  background-color: #f3f3f3;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: .6px;
  left: .6px;
  right: .6px;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
}

.switch-input:focus + .switch-slider {
  box-shadow: 0 0 1px #b1b1ae;
}

.switch-input:checked + .switch-slider {
  background-color: var(--accent-color);
}

.switch-input:checked + .switch-slider:before {
  transform: translateX(26px);
}

@media screen and (min-width: 768px) {
  .header-burger {
    display: none;
